.start-menu {
  position: fixed;
  bottom: 60px;
  left: 50%;
  transform: translateX(-50%);
  width: 850px;
  height: 700px;
  background: rgba(30, 30, 30, 0.95);
  color: white;
  display: flex;
  border-radius: 15px;
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.3);
  overflow: hidden; 
}

.start-sidebar {
  width: 150px;
  background: rgba(20, 20, 20, 0.9);
  padding: 10px;
}

.start-sidebar ul {
  list-style: none;
  padding: 0;
}

.start-sidebar li {
  padding: 15px;
  font-size: 18px;
  display: flex;
  align-items: center;
  gap: 10px;
  cursor: pointer;
  transition: 0.3s ease;
}

.start-sidebar li:hover, .start-sidebar li.active {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 6px;
}

.start-content {
  flex: 1;
  padding: 20px;
}

.experience-section ul, .projects-section ul {
  list-style: none;
  padding: 0;
}

.experience-section li, .projects-section li {
  background: rgba(255, 255, 255, 0.1);
  padding: 10px;
  margin: 5px 0;
  border-radius: 6px;
}

.tech-icons {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
}

.tech-icon {
  width: 60px;
  height: 60px;
}

.start-menu-footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  display: flex;
  justify-content:right;
  align-items: center;
  padding: 15px 20px;
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  box-sizing: border-box;
}

.social-icons {
  display: flex;
  align-items: center;
  gap: 10px; 
}

.mss-text {
  color: #d0e1ff;
  font-size: 14px;
  font-weight: bold;
}

.social-icon {
  font-size: 20px;
  padding-left: 10px;
  color: #d0e1ff; 
  transition: transform 0.3s ease, color 0.3s ease;
}

.social-icon:hover {
  transform: scale(1.3);
  color: #4689f5;
}
