.taskbar {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 50px;
  background: rgba(34, 34, 34, 0.8);
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: white;
  padding: 0 15px;
  box-sizing: border-box;
  z-index: 1000;
}

.center-section {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10;
}

.start-btn {
  background: none;
  border: none;
  color: white;
  cursor: pointer;
  font-size: 18px;
  padding: 5px 10px;
  border-radius: 5px;
  transition: background 0.3s;
}

.start-btn:hover {
  background: rgba(255, 255, 255, 0.2);
}


.docked-icons {
  display: flex;
  align-items: center;
  gap: 10px;
  position: absolute;
  left: 51.5%; 
  transform: translateX(0);
  z-index: 9;
  transition: all 0.3s ease-in-out;
}

.docked-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5px;
  background: rgba(255, 255, 255, 0.2);
  border: none;
  padding: 5px 10px;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  color: white;
  font-size: 14px;
  flex-shrink: 0;
}

.docked-icon:hover {
  background: rgba(255, 255, 255, 0.4);
}

.docked-icon img {
  width: 24px;
  height: 24px;
  border-radius: 3px;
}

.right-section {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  text-align: right;
  font-size: 14px;
  flex-shrink: 0;
}